import { Form, Input, notification } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { BiSearch } from 'react-icons/bi';
import { CgSpinner } from 'react-icons/cg';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import actions from '../../../redux/transaction/actions';
import { currencyFormat } from '../../../utils/helper/utilities';
import RowItemPrint from '../widget/RowItemPrint';

export default function PriceCheckPage() {
	const inputRef = useRef(null);
	const dispatch = useDispatch();
	const history = useHistory();

	const { loading = false, searchPriceResult = [] } = useSelector((state) => state?.transaction);

	const [form] = Form.useForm();

	const getDataResult = () => {
		if (searchPriceResult.length > 0) {
			return searchPriceResult[0];
		}

		return {};
	};

	const [state, setState] = useState({
		barcode: ''
	});

	const handleFocus = () => {
		if (inputRef && inputRef.current !== null) {
			inputRef.current.focus();
			return;
		}
	};

	const handleResetForm = () => {
		form.resetFields();
		handleFocus();

		dispatch({
			type: actions.SET_STATE,
			payload: {
				searchPriceResult: []
			}
		});
	};

	const handleSearchItem = (query = '') => {
		if (query && query.length !== 0) {
			if (query.length >= 6) {
				dispatch({
					type: 'transaction/SEARCH_PRICE',
					payload: {
						query
					}
				});

				handleResetForm();
			} else {
				notification.warning({
					message: 'Perhatian!',
					description: 'Masukkan kode yang benar! Min. kode adalah 6 digit'
				});

				handleFocus();
				handleResetForm();
			}

			return;
		}

		handleResetForm();
		notification.warning({
			message: 'Perhatian!',
			description: 'Masukkan kode yang terlebih dahulu.'
		});
	};

	const handleKeyDown = (e) => {
		if (e.key === 'End' || e.key === 'Escape') {
			history.push('/pos/kasir');
		}

		handleFocus();
	};

	useEffect(() => {
		handleFocus();
	});

	useEffect(() => {
		handleFocus();

		document.addEventListener('keydown', handleKeyDown);
		return () => {
			document.removeEventListener('keydown', handleKeyDown);
		};
	}, []);

	useEffect(() => {
		handleFocus();
	}, [state]);

	return (
		<div className="w-full h-screen overflow-hidden overflow-y-scroll">
			<div className="w-full h-full flex items-center justify-center">
				<div className="w-2/3">
					<div className="w-full h-40 bg-primary rounded-lg flex items-center justify-end p-5">
						<h1 className="text-6xl text-white font-bold">IDR {currencyFormat(getDataResult().SalesPrice, 0)}</h1>
					</div>
					<div className="my-5">
						<Form form={form} autoFocus>
							<Form.Item name="search-item">
								<Input
									id="scan-item-search"
									value={state?.barcode}
									ref={inputRef}
									autoFocus
									className={`w-full rounded-lg px-4 text-lg placeholder-gray-800 focus:border-2 focus:border-gray-500 custom-input`}
									placeholder="Barcode / PLU Barang"
									suffix={
										loading ? (
											<CgSpinner className="animate-spin text-xl text-gray-500" />
										) : (
											<BiSearch className="text-xl text-gray-500" />
										)
									}
									style={{ height: '68px' }}
									onPressEnter={(event) => {
										handleSearchItem(event.target.value);
									}}
									onChange={(event) => {
										setState((prev) => ({
											...prev,
											barcode: event.target.value
										}));
									}}
								/>
							</Form.Item>
						</Form>
					</div>
					<div className="w-full">
						<RowItemPrint title="Kode Barang/PLU" value={<b>{getDataResult().ItemCode}</b>} />
						<RowItemPrint title="Barcode" value={<b>{getDataResult().upcBarcode}</b>} />
						<RowItemPrint title="Nama Barang" value={<b>{getDataResult().ItemName}</b>} />
						<RowItemPrint title="Satuan" value={<b>{getDataResult().UomStock}</b>} />
					</div>
				</div>
			</div>
		</div>
	);
}
